import { BehaviorSubject, Observable, of } from 'rxjs';
import { Ilog } from './../models/log';
import { Injectable } from '@angular/core';



@Injectable({
  providedIn: 'root'
})
export class LogsService {
  logs: Ilog[];
  logSourceRecuperud = new BehaviorSubject<Ilog>({ id: null, title: null, date: null });
  recuperdLog = this.logSourceRecuperud.asObservable();
  constructor() {
    this.logs = [
      // {
      //   id: '1',
      //   title: 'Action massive et stratégique',
      //   date: new Date('12/23/2018 14:23:55'),
      // },
      // {
      //   id: '2',
      //   title: 'Le point de non retour',
      //   date: new Date('10/16/2018 10:22:44'),
      // },
      // {
      //   id: '3',
      //   title: 'la joie de l\'homme est dans l\'action',
      //   date: new Date('04/28/2018 00:22:44'),
      // }
    ];
  }
  getlogs(): Observable<Ilog[]> {
    if (localStorage.getItem('logs') === null) {
      this.logs = [];
    } else {
      this.logs = JSON.parse(localStorage.getItem('logs'));
    }
    return of(this.logs.sort((a, b) => {
      return b.date = a.date;
    }));
  }
  setLogSourceRecupered(log: Ilog) {
    this.logSourceRecuperud.next(log);
  }
  addLog(log: Ilog) {
    this.logs.unshift(log);
    localStorage.setItem('logs', JSON.stringify(this.logs));

  }
  updateLog(modifiedLog: Ilog) {
    this.logs.forEach((cur, index) => {
      if (modifiedLog.id === cur.id) {
        this.logs.splice(index, 1);
      }
    });
    this.logs.unshift(modifiedLog);
    localStorage.setItem('logs', JSON.stringify(this.logs));
  }
  deletelog(log: Ilog) {
    this.logs.forEach((cur, index) => {
      if (cur.id === log.id) {
        this.logs.splice(index, 1);
      }
    });
    localStorage.setItem('logs', JSON.stringify(this.logs));
  }


}
