import { Ilog } from './../../models/log';
import { LogsService } from './../../services/logs.service';
import { Component, OnInit } from '@angular/core';
import { Title } from '@angular/platform-browser';

@Component({
  selector: 'app-log-form',
  templateUrl: './log-form.component.html',
  styleUrls: ['./log-form.component.css']
})
export class LogFormComponent implements OnInit {
  isNew: Boolean = true;
  selectedLog: Ilog = {
    id: '',
    title: '',
    date: null
  };
  constructor(private _logService: LogsService) { }

  ngOnInit() {
    this._logService.recuperdLog.subscribe((log) => {

      if (log.id !== null) {
        this.isNew = false;
        this.selectedLog = log;
      }

    });

  }
  onSubmit() {
    if (this.isNew) {
      const newLog: Ilog = {
        id: this.generateId(),
        title: this.selectedLog.title,
        date: new Date()
      };
      this._logService.addLog(newLog);

    } else {
      const modifiedLog: Ilog = {
        id: this.selectedLog.id,
        title: this.selectedLog.title,
        date: new Date()
      };
      this._logService.updateLog(modifiedLog);

    }
    this.clearForm();
  }
  clearForm() {
    this.isNew = true;
    this.selectedLog = {
      id: '',
      title: '',
      date: null
    };


  }
  generateId() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
      // tslint:disable-next-line:no-bitwise
      const r = Math.random() * 16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8);
      return v.toString(16);
    });
  }
}
