import { Ilog } from './../../models/log';
import { Component, OnInit } from '@angular/core';
import { LogsService } from '../../services/logs.service';

@Component({
  selector: 'app-logs',
  templateUrl: './logs.component.html',
  styleUrls: ['./logs.component.css']
})
export class LogsComponent implements OnInit {
  logs: Ilog[];
  selectedLog: Ilog;
  loaded = false;
  constructor(private _logsService: LogsService) { }

  ngOnInit() {
    // this.logs = this._logsService.getlogs();
    this._logsService.getlogs().subscribe((logs) => { this.logs = logs; this.loaded = true; });
    this.selectedLog = {
      id: '',
      title: '',
      date: null
    };
  }
  onSelect(log) {
    this._logsService.setLogSourceRecupered(log);
    this.selectedLog = log;
    console.log(this.selectedLog);
  }
  onDelete(log: Ilog) {
    if (confirm('Are you sure to delete this?')) {
      this._logsService.deletelog(log);
    }

  }



}
